.catalog-sections {
    padding: 20px;

    @media (min-width: $breakpoint-large) {
        padding-top: 82px;
    }
}

.catalog-sections__container {
    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 80px;
    }
}

.catalog-sections__item {
    position: relative;
    padding: 24px 20px 20px;
    background-color: $color-gray-50;

    @media (min-width: $breakpoint-medium) {
        padding: 77px 316px 52px 52px;
    }

    @media (min-width: $breakpoint-large) {
        padding: 47px 52px 55px;
    }

    & + & {
        margin-top: 20px;

        @media (min-width: $breakpoint-medium) {
            margin-top: 30px;;
        }

        @media (min-width: $breakpoint-large) {
            margin-top: 0;
        }
    }
}

.catalog-sections__heading {
    position: relative;
    min-height: 50px;
    margin: 0;
    margin-bottom: 22px;
    padding-left: 68px;
    display: flex;
    align-items: center;
    font-family: $font-heading;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 36px;

    @media (min-width: $breakpoint-medium) {
        position: static;
        padding-left: 0;
        font-size: 36px;
    }

    @media (min-width: $breakpoint-large) {
        position: relative;
        min-height: 100px;
        margin-bottom: 39px;
        padding-left: 162px;
    }

    &::before,
    &::after {
        content: "";
        position: absolute;
    }

    &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 50px;
        height: 50px;
        background-color: $color-primary-500;
        border-radius: 50%;

        @media (min-width: $breakpoint-medium) {
            top: calc(50% - 100px);
            right: 62px;
            left: auto;
            width: 200px;
            height: 200px;
        }

        @media (min-width: $breakpoint-large) {
            top: calc(50% - 50px);
            right: auto;
            left: 0;
            width: 100px;
            height: 100px;
        }
    }

    &::after {
        background-size: contain;
    }
}

.catalog-sections__heading--slim {
    &::after {
        top: -3px;
        left: 7px;
        width: 36px;
        height: 50px;
        background-image: url("../img/icons/cat-slim.svg");

        @media (min-width: $breakpoint-medium) {
            top: calc(50% - 110px);
            right: 92px;
            left: auto;
            width: 140px;
            height: 194px;
        }

        @media (min-width: $breakpoint-large) {
            top: calc(50% - 54px);
            left: 15px;
            width: 70px;
            height: 97px;
        }
    }
}

.catalog-sections__heading--pro {
    &::after {
        top: 11px;
        left: -10px;
        width: 67px;
        height: 28px;
        background-image: url("../img/icons/cat-pro.svg");

        @media (min-width: $breakpoint-medium) {
            top: calc(50% - 56px);
            right: 28px;
            left: auto;
            width: 268px;
            height: 112px;
        }

        @media (min-width: $breakpoint-large) {
            top: calc(50% - 22px);
            left: -19px;
            width: 134px;
            height: 56px;
        }
    }
}

.catalog-sections__text {
    margin: 0;
    margin-bottom: 15px;
    padding-bottom: 23px;
    color: $color-gray-700;
    border-bottom: 1px solid $color-gray-200;

    @media (min-width: $breakpoint-medium) {
        margin-bottom: 48px;
        border: none;
    }

    @media (min-width: $breakpoint-large) {
        margin-bottom: 23px;
    }
}

.catalog-sections__link {
    display: grid;
    grid-template-columns: max-content 32px;
    align-items: center;
    gap: 17px;

    color: $color-black;
    font-family: $font-heading;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;

    @media (min-width: $breakpoint-medium) {
        font-size: 20px;
    }

    &::after {
        content: "";
        width: 24px;
        height: 10px;
        background-repeat: no-repeat;
        background-position: 100% 50%;
        background-image: url("../img/icons/arrow.svg");
    }

    &:hover {
        &::after {
            width: 32px
        }
    }

    &:active {
        opacity: 0.3;

        &::after {
            width: 32px;
        }
    }
}

