.form {
    overflow: hidden;

    @media (min-width: $breakpoint-large) {
        padding-top: 26px;
    }
}

.form__container {
    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 80px;
    }
}

.form__group {
    margin: 0;

    & + & {
        margin-top: 25px;

        @media (min-width: $breakpoint-medium) {
            margin-top: 70px;
        }

        @media (min-width: $breakpoint-large) {
            &.form__group--target {
                margin-top: 0;
            }
        }
    }
}

.form__group--contacts,
.form__group--comment,
.form__group--additional {
    @media (min-width: $breakpoint-large) {
        grid-column: 1 / -1;
    }
}

.form__footer {
    @media (min-width: $breakpoint-large) {
        grid-column: 1 / -1;
    }
}

.form__group--target {
    margin-right: -21px;
    margin-left: -21px;
    border: 1px solid $color-gray-200;

    @media (min-width: $breakpoint-medium) {
        margin-right: 0;
        margin-left: 0;
    }
}

.form__footer {
    margin-top: 34px;
    padding-bottom: 32px;
    text-align: center;

    @media (min-width: $breakpoint-medium) {
        margin-top: 54px;
        padding-bottom: 72px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        gap: 60px;
        text-align: left;
    }

    @media (min-width: $breakpoint-large) {
        grid-column: 1 / -1;
    }
}

.form__button {
    width: 100%;

    @media (max-width: $breakpoint-small-down) {
        margin-bottom: 21px;
    }
}

.form__additional {
    margin: 0;
}